body{
    font-size: 2rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #124552;
}
button{
    cursor: pointer;
   width: 100%;
    max-width: 150px;
    padding: 5px 10px;
    margin-right: 20px;
    border-radius: 5px;
    border: none;
    background-color: #124552;
    color: azure;
    font-size: 1.5rem;
}
button:hover{
    transition: .75s ease;
    transform: rotate(15deg)
}
.wrap{
    margin: 0 auto;
    max-width: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.btn_wrap{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
}